import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { getPointOnArc } from './utils/paths';
import { mapRange, colorToString } from './utils/utils';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const drawDot = ({ x, y }) => {
  ctx.beginPath();
  ctx.arc(x, y, 2, 0, Math.PI * 2);
  ctx.fill();
};

const circlePoint = (cx, cy, radius, speed) => {
  let _cx = cx;
  let _cy = cy;
  let _radius = radius;
  let _speed = speed;

  let prevX = 0;
  let prevY = 0;

  let t = 0;

  let { x, y } = getPointOnArc(_cx, _cy, t % Math.PI * 2, _radius);

  const update = (debug = false) => {
    const pos = getPointOnArc(_cx, _cy, t % Math.PI * 2, _radius);

    prevX = x;
    prevY = y;
    x = pos.x;
    y = pos.y;

    t += _speed;

    if (debug) {
      drawDot({ x, y });
    }
  }

  const position = () => {
    return { x, y };
  }

  const previous = () => {
    return { x: prevX, y: prevY };
  }

  update();

  return {
    update,
    previous,
    position
  };
}

const drawLine = ({ x: x1, y: y1 }, { x: x2, y: y2 }) => {
  ctx.beginPath();
  ctx.moveTo(x1, y1);
  ctx.lineTo(x2, y2);
  ctx.stroke();
}

const getRandom = (speed) => {
  const fidelity = 20;

  return (Math.round(Math.random() * fidelity) / fidelity) / speed;
};

const SPEED = 25;
const timer = circlePoint(WIDTH / 2, HEIGHT / 2, WIDTH / 4, getRandom(SPEED));
const timer2 = circlePoint(WIDTH / 2, HEIGHT / 2, WIDTH / 4, getRandom(SPEED));

app.onRender(({ frame, width, height, debug }) => {
  // ctx.clearRect(0, 0, width, height);
  ctx.save();
  ctx.globalAlpha = 0.02;
  ctx.fillStyle = 'white';
  ctx.fillRect(0, 0, width, height);
  ctx.restore();

  const { x: x1 } = timer.position();
  const { y: y1 } = timer2.position();

  const { x: x2 } = timer.previous();
  const { y: y2 } = timer2.previous();

  ctx.lineWidth = 4;
  ctx.lineCap = 'round';
  ctx.strokeStyle = colorToString({
    r: 255,
    g: mapRange(x1, 0, width, 0, 255),
    b: mapRange(y1, 0, height, 0, 255),
  })
  drawLine({
    x: x1,
    y: y1
  }, {
    x: x2,
    y: y2
  });

  ctx.save();
  ctx.fillStyle = '#ccc';
  drawDot({ x: x1, y: 0 });
  drawDot({ x: 0, y: y1 });
  ctx.restore();

  timer.update();
  timer2.update();
});

app.start();
